import { GET_USER, LOADING_USER, UPDATE_USER, UPDATING } from './types';
import axios from 'axios';

export const getUser = (token) => async (dispatch) => {
	if (token) {
		dispatch({ type: LOADING_USER });
		const response = await axios.get('/api/v1/ecartusers/user', {
			headers: { authorization: `Bearer ${token}` },
		});

		dispatch({ type: GET_USER, payload: response.data.data });
	}
};

export const updateUser = (values, token) => async (dispatch) => {
	dispatch({ type: UPDATING });
	const response = await axios.post(
		'/api/v1/ecartusers/updateuser',
		{ ...values },
		{ headers: { authorization: `Bearer ${token}` } }
	);
	if (response.data.status === 'Success') {
		dispatch({ type: UPDATE_USER, payload: response.data.data });
	}
};
