import {
	GET_PRODUCT,
	LOADING_PRODUCT,
	GET_PRODUCTS,
	GET_REVIEWS,
	LOADING_PRODUCTS,
	CHECK_PRODUCT_IN_CART,
	GET_ORDERS,
	LOADING_ORDERS,
} from './types';

import axios from 'axios';

export const getProducts = (name) => (dispatch) => {
	dispatch({ type: LOADING_PRODUCTS });
	axios.get(`/api/v1/ecartproducts/category/${name}`).then((response) => {
		dispatch({
			type: GET_PRODUCTS,
			payload: { products: [...response.data.data], category: name },
		});
	});
};

export const getProduct = (id) => async (dispatch) => {
	dispatch({ type: LOADING_PRODUCT });
	const productResponse = await axios.get(
		`/api/v1/ecartproducts/product/${id}`
	);

	const reviewsResponse = await axios.get(
		`/api/v1/ecartproducts/product/${id}/reviews`
	);

	dispatch({
		type: CHECK_PRODUCT_IN_CART,
		payload: productResponse.data.data._id,
	});

	dispatch({
		type: GET_PRODUCT,
		payload: {
			product: productResponse.data.data,
			reviews: [...reviewsResponse.data.data],
		},
	});
};

export const getOrders = (token) => async (dispatch) => {
	dispatch({ type: LOADING_ORDERS });
	const response = await axios.get('/api/v1/ecartproducts/orders', {
		headers: { authorization: `Bearer ${token}` },
	});
	dispatch({ type: GET_ORDERS, payload: response.data.data });
};
