import { GET_ORDERS, LOADING_ORDERS } from '../Actions/types';

const initialState = {
	data: [],
	loading: false,
};

function orderReducer(state = initialState, action) {
	switch (action.type) {
		case GET_ORDERS:
            return { data: action.payload , loading: false };
        case LOADING_ORDERS:
            return {...state, loading:true}

        default:
            return state;
	}
}


export default orderReducer;