import { GET_USER, LOADING_USER, UPDATE_USER, UPDATING } from '../Actions/types';

const initialState = {
	firstName: '',
	lastName: '',
	email: '',
	mobileNo: null,
	address: '',
	gender: '',
	loading: true,
	updating: false,
};

function userReducer(state = initialState, action) {
	switch (action.type) {
		case GET_USER:
			return {
				...state,
				...action.payload,
				loading: false,
			};
		case LOADING_USER:
			return {
				...state,
				loading: true,
			};
		case UPDATING:
			return {
				...state,
				updating: true,
			}
		case UPDATE_USER:
			return {
				...state,
				...action.payload,
				updating: false,
			};
		default:
			return state;
	}
}

export default userReducer;
