import {
  USER_AUTH,
  LOADING_AUTH,
  LOGOUT_USER,
  GET_USER,
  AUTH_ERROR,
  CLEAR_ERROR,
} from "../Actions/types";
import Cookies from "js-cookie";

const token = () => Cookies.get("jwt");

const initialState = {
  isAuth: token() ? true : false,
  token: token(),
  authError: null,
  loading: false,
  error: "",
};

function authReducer(state = initialState, action) {
  switch (action.type) {
    case GET_USER:
      return {
        ...state,
        user: action.payload,
        loading: false,
      };
    case USER_AUTH:
      return {
        ...state,
        token: token(),
        isAuth: true,
        loading: false,
      };

    case LOADING_AUTH:
      return { ...state, loading: true };

    case AUTH_ERROR:
      return { ...state, error: action.payload, loading: false };

    case CLEAR_ERROR:
      return { ...state, error: "" };

    case LOGOUT_USER:
      Cookies.remove("jwt");
      return {
        ...state,
        token: null,
        isAuth: false,
      };

    default:
      return state;
  }
}

export default authReducer;
